import React, { ReactElement } from 'react';

import { IVideo } from '@utils/interfaces';

import { Description, Wrapper, Circle, Container, Link, Text } from './styles';

const Card = ({
  image,
  date,
  title,
  duration,
  views,
  owner_id,
  id,
  onClick,
}: IVideo & { onClick: (event: Event, sortingType: string) => void }): ReactElement => {
  return (
    <Link href={`https://vk.com/video${owner_id}_${id}`} target="_blank" rel="noreferrer">
      <Wrapper url={image[3]?.url}>
        <Description>
          <Container>
            <Circle color="orange" />
            <span>{date}</span>
          </Container>
          <span onClick={(event: any) => onClick(event, 'time')}>time:&nbsp;{duration}</span>
          <span onClick={(event: any) => onClick(event, 'views')}>{views}&nbsp;views</span>
        </Description>
        <Text>
          <span>{title}</span>
        </Text>
      </Wrapper>
    </Link>
  );
};

export default Card;
