import React, { ReactElement, useState, useEffect } from 'react';
import { useSelector } from 'react-redux';

import { IStore } from '@app/store';
import Card from '@components/Card';
import Loader from '@components/Loader';
import { IState } from '@store/videos/interfaces';
import { IVideo } from '@utils/interfaces';

import { IProps } from './interfaces';
import { Wrapper, LoaderWrapper } from './styles';

const Results = ({ is2160p, isReverse, is1080p }: IProps): ReactElement => {
  const [timeSort, setTimeSort] = useState<number>(0);
  const [viewSort, setViewSort] = useState<number>(0);
  const getVideos = (store: IStore): IState => store.videos;

  const { videos, isLoading } = useSelector(getVideos);
  const [videosArr, setVideosArr] = useState<IVideo[]>(videos);

  const handleClick = (event: Event, sortingType: string): void => {
    event.preventDefault();

    switch (sortingType) {
      case 'time': {
        setTimeSort(timeSort > 1 ? 0 : timeSort === 1 ? 2 : 1);
        break;
      }
      case 'views': {
        setViewSort(viewSort > 1 ? 0 : viewSort === 1 ? 2 : 1);
        break;
      }
    }
  };

  const filter1080 = (): void => {
    if (is1080p) {
      const newVideos = videosArr.filter(({ width }: IVideo) => width >= 1920);
      videosArr.forEach(({ width, height }) => {
        if (width > 1920) {
          console.log({ width, height });
        }
      });
      setVideosArr(newVideos);
    } else {
      setVideosArr(videos);
    }
  };

  const filter2160 = (): void => {
    if (is2160p) {
      const newVideos = videosArr.filter(({ width }: IVideo) => width >= 2160);

      setVideosArr(newVideos);
    } else {
      setVideosArr(videos);
    }
  };

  const filterReverse = (): void => {
    if (isReverse) {
      setVideosArr(videosArr.reverse());
    } else {
      setVideosArr(videosArr);
    }
  };

  useEffect(() => {
    switch (viewSort) {
      case 0: {
        setVideosArr(videos);
        break;
      }
      case 1: {
        const sortedVideos = videosArr
          .slice()
          .sort((a: IVideo, b: IVideo): number => a.views - b.views);
        setVideosArr(sortedVideos);
        break;
      }
      case 2: {
        const sortedVideos = videosArr
          .slice()
          .sort((a: IVideo, b: IVideo) => a.views - b.views)
          .reverse();
        setVideosArr(sortedVideos);
        break;
      }
    }
  }, [viewSort]);

  useEffect(() => {
    setVideosArr(videos);
  }, [videos]);

  useEffect(filterReverse, [isReverse]);
  useEffect(filter1080, [is1080p]);
  useEffect(filter2160, [is2160p]);

  console.log({ videosArr });

  const renderVideoCard = (card: IVideo): ReactElement => (
    <Card key={card.id + Math.random()} {...card} onClick={handleClick} />
  );

  return (
    <Wrapper>
      {isLoading && (
        <LoaderWrapper>
          <Loader />
        </LoaderWrapper>
      )}
      {!isLoading && videosArr?.map(renderVideoCard)}
    </Wrapper>
  );
};

export default Results;
