import { IVideoRequest } from '@utils/interfaces';

import { IForm } from './interfaces';

export const DEFAULT_FORM_VALUES: IForm = {
  search: '',
  sorting: 0,
  isAdult: true,
  count: 200,
  offset: 0,
  longer: 12,
  shorter: 0,
};

export const normalizeSearchRequest = ({
  isAdult,
  count,
  offset,
  longer,
  shorter,
  search,
  sorting,
}: IForm): IVideoRequest => ({
  adult: isAdult ? 1 : 0,
  count,
  hd: 0,
  shorter: shorter === 0 ? '' : shorter * 60,
  longer: longer === 0 ? '' : longer * 60,
  offset,
  q: search,
  sort: sorting,
  v: '5.131',
});
