import React, { ReactElement } from 'react';

import { MenuItem, InputLabel } from '@mui/material';

import { IOption, IProps } from './interfaces';
import { SelectUI, FormControl } from './styles';

const Select = ({ labelId, id, name, value, label, onChange, options }: IProps): ReactElement => {
  const renderOption = ({ label, value }: IOption): ReactElement => (
    <MenuItem key={value} value={value}>
      {label}
    </MenuItem>
  );

  return (
    <FormControl style={{ color: 'white' }} fullWidth>
      <InputLabel id={labelId}>{label}</InputLabel>
      <SelectUI
        labelId={labelId}
        id={id}
        name={name}
        value={value}
        label={label}
        onChange={onChange}
      >
        {options?.map(renderOption)}
      </SelectUI>
    </FormControl>
  );
};

export default Select;
