import styled from 'styled-components';

import { IStylesBase } from '@app/palette';

const breakpointFromTablet = ({ theme }: IStylesBase): string => theme.media.tablet;

export const Wrapper = styled.div`
  padding-top: 70px;
  position: relative;
  display: flex;
  flex-direction: column;

  @media ${breakpointFromTablet} {
    flex-direction: row;
  }

  @media only screen and (min-width: 1024px) and (max-width: 1367px) and (max-height: 1366px) and (-webkit-min-device-pixel-ratio: 1.5) {
    flex-direction: column;
    width: calc(100% - 10px);
  }
`;
