import { FormikValues, useFormik } from 'formik';
import React, { ReactElement } from 'react';
import { useDispatch } from 'react-redux';

import Checkbox from '@components/Checkbox';
import Input from '@components/Input';
import Select from '@components/Select';
import UIWrapper from '@components/UIWrapper';
import { searchVideos } from '@store/videos';

import { IForm, IProps } from './interfaces';
import { Wrapper, Form, Button, Filters } from './styles';
import { DEFAULT_FORM_VALUES, normalizeSearchRequest } from './utils';

const Search = ({ is1080p, onFilterChange, is2160p, isReverse }: IProps): ReactElement => {
  const dispatch = useDispatch();

  const onSubmit = (values: FormikValues): void => {
    dispatch(searchVideos(normalizeSearchRequest(values as IForm)));
  };

  const form = {
    initialValues: DEFAULT_FORM_VALUES,
    onSubmit,
  };

  const {
    values: { search, isAdult, sorting, shorter, longer },
    handleChange,
    handleSubmit,
  } = useFormik(form);

  return (
    <Wrapper>
      <UIWrapper>
        <Form onSubmit={handleSubmit}>
          <Input
            autoFocus={true}
            label="Ищем"
            id="search"
            name="search"
            value={search}
            onChange={handleChange}
          />
          <Input
            label="От"
            id="longer"
            name="longer"
            value={longer}
            onChange={handleChange}
            placeholder="Введите число"
          />
          <Input
            label="До"
            id="shorter"
            name="shorter"
            value={shorter}
            onChange={handleChange}
            placeholder="Введите число"
          />
          <Select
            labelId="demo-simple-select-label"
            id="sorting"
            name="sorting"
            value={sorting}
            label="Сортировать"
            onChange={handleChange}
            options={[
              { label: 'по дате', value: 0 },
              { label: 'по длительности', value: 1 },
              { label: 'по релевантности', value: 2 },
            ]}
          />
          <Checkbox
            onChange={handleChange}
            id="isAdult"
            value={isAdult}
            name="isAdult"
            label="Видео для взрослых"
          />
          <Button variant="outlined" type="submit">
            Search
          </Button>
        </Form>
        <Filters>
          <Checkbox
            onChange={onFilterChange}
            id="is1080p"
            value={is1080p}
            name="is1080p"
            label="Видео Full HD"
          />
          <Checkbox
            onChange={onFilterChange}
            id="is2160p"
            value={is2160p}
            name="is2160p"
            label="Видео 4k"
          />
          <Checkbox
            onChange={onFilterChange}
            id="isReverse"
            value={isReverse}
            name="isReverse"
            label="Обратный порядок"
          />
        </Filters>
      </UIWrapper>
    </Wrapper>
  );
};

export default Search;
