export const SelectUI = styled(Select)`
  position: relative;
`;

export const FormControl = styled(FormControlUI)`
  max-width: 180px;

  label,
  svg {
    color: ${setColorBlue};
  }

  div {
    color: white;
    border-radius: 8px;

    &:first-child {
      padding: 8.5px 14px;
    }

    @media only screen and (min-width: 1024px) and (max-width: 1367px) and (max-height: 1366px) and (-webkit-min-device-pixel-ratio: 1.5) {
      margin-right: 15px;
    }
  }

  fieldset {
    border-color: ${setColorBlue} !important;
  }
`;
