import moment from 'moment';

import { createSlice, Draft, PayloadAction, Dispatch } from '@reduxjs/toolkit';

import API from '@app/utils';
import { ISearchVideoResponse, IVideo, IVideoRequest } from '@utils/interfaces';

import { IState, TDispatchCallback } from './interfaces';

const initialState: IState = {
  isLoading: false,
  videos: [],
};

const setLoading = (state: Draft<IState>, { payload }: PayloadAction<boolean>): Draft<IState> => ({
  ...state,
  isLoading: payload,
});

export const searchVideos =
  (params: IVideoRequest): TDispatchCallback =>
  (dispatch: Dispatch): void => {
    console.log({ t: params.offset });
    dispatch(actions.setLoading(true));
    API.searchVideos(params, (responseData: ISearchVideoResponse) =>
      handleGetVideos(responseData, params, dispatch)
    );
  };

const handleGetVideos = (
  { response: { items } }: ISearchVideoResponse,
  params: IVideoRequest,
  dispatch: Dispatch
): void => {
  const formattedVideos = items
    .map((video: IVideo) => ({
      ...video,
      duration: `${Math.trunc(parseInt(video.duration.toString(), 10) / 60)}:${
        parseInt(video.duration.toString(), 10) % 60
      }`,
      date:
        typeof video.date === 'number'
          ? moment(new Date(video.date * 1000).toString()).fromNow()
          : video.date,
    }))
    .filter(({ duration }: IVideo) => duration !== '0:0');

  dispatch(actions.setVideos(formattedVideos));
  dispatch(actions.setLoading(false));

  if (params.offset < 1000) {
    const newParams = { ...params, offset: params.offset + 200 };

    dispatch(searchVideos(newParams) as any);
  }
};

const setVideos = (state: Draft<IState>, { payload }: PayloadAction<IVideo[]>): Draft<IState> => ({
  ...state,
  videos: [...state.videos, ...payload],
});

export const videos = createSlice({
  name: 'Videos',
  initialState,
  reducers: {
    setLoading,
    setVideos,
  },
});

export const { reducer, actions } = videos;

export default reducer;
