import {
  IUserData,
  IVideoRequest,
  TCallbackLogin,
  TCallbackSearchVideo,
  TCallbackUser,
} from './interfaces';

declare global {
  interface Window {
    VK: {
      Api: {
        call: (
          type: string,
          data: IUserData | IVideoRequest,
          callback: TCallbackUser | TCallbackSearchVideo
        ) => void;
      };
      Auth: {
        login: (callback: TCallbackLogin, type: number) => void;
      };
      init: (data: { apiId: number }) => void;
    };
  }
}

const init = (): void => {
  window.VK.init({
    apiId: 7475970,
  });
};

const login = (callback: TCallbackLogin, type: number): void => {
  window.VK.Auth.login(callback, type);
};

const getUser = (id: string, callback: TCallbackUser): void => {
  const data = {
    user_ids: id,
    fields: 'photo_big',
    name_case: 'Nom',
    v: '5.126',
  };

  window.VK.Api.call('users.get', data, callback);
};

const searchVideos = (request: IVideoRequest, callback: TCallbackSearchVideo): void => {
  window.VK.Api.call('video.search', request, callback);
};

const API = {
  init,
  login,
  getUser,
  searchVideos,
};

export default API;
