import { FormikValues, useFormik } from 'formik';
import React, { ReactElement } from 'react';

import Results from '@pages/Layout/components/Results';
import Search from '@pages/Layout/components/Search';

import { DEFAULT_FILTERS } from './constants';
import { Wrapper } from './styles';

const Content = (): ReactElement => {
  const onSubmit = (values: FormikValues): void => {
    console.log('');
  };

  const form = {
    initialValues: DEFAULT_FILTERS,
    onSubmit,
  };

  const {
    values: { is1080p, isReverse, is2160p },
    handleChange,
  } = useFormik(form);

  return (
    <Wrapper>
      <Search
        is1080p={is1080p}
        isReverse={isReverse}
        is2160p={is2160p}
        onFilterChange={handleChange}
      />
      <Results is1080p={is1080p} isReverse={isReverse} is2160p={is2160p} />
    </Wrapper>
  );
};

export default Content;
