import { PayloadAction, createSlice, Draft } from '@reduxjs/toolkit';

import { IAuthResponse, IUser } from '@utils/interfaces';

import { DEFAULT_SESSION, DEFAULT_USER_DATA } from './constants';
import { IState } from './interfaces';

const initialState: IState = {
  session: DEFAULT_SESSION,
  user: DEFAULT_USER_DATA,
};

const setAuthData = (
  state: Draft<IState>,
  { payload: { session } }: PayloadAction<IAuthResponse>
): Draft<IState> => ({
  ...state,
  session,
});

const setUserData = (state: Draft<IState>, { payload }: PayloadAction<IUser>): Draft<IState> => ({
  ...state,
  user: payload,
});

export const user = createSlice({
  name: 'User',
  initialState,
  reducers: {
    setAuthData,
    setUserData,
  },
});

export const { reducer, actions } = user;

export default reducer;
